.swiper-button-next::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 55%;
  background: no-repeat url(../../../../assets/images/state/button-next.svg);
  transform: translate(-50%, -50%) scale(0.4);
  transition: all 0.3s linear;
}

.swiper-button-prev::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 55%;
  background: no-repeat url(../../../../assets/images/state/arrow-prev.svg);
  transform: translate(-50%, -50%) scale(0.4);
  transition: all 0.3s linear;
}

.swiper-button-disabled:before {
  opacity: 0.4;
}
